@import url(https://fonts.googleapis.com/css?family=Open+Sans:bold,normal&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand:bold,normal&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Quicksand', Arial, Helvetica, sans-serif;
}

body {
  font-family: 'Quicksand', Arial, Helvetica, sans-serif;
}


a {
  text-decoration: none;
}
.App-header {
    background-color: #282c34;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 24px;
    padding: 0px 20px;
  }
  
    .header-text {
      color: white;
    }

    .header-links {
        display: flex;
        align-items: center;
    }
        .header-link {
            padding: 20px;
            font-size: 20px;
        }
        .header-link:hover {
            background-color: #139b21;
            padding: 20px;
            font-weight: bold;
        }

@media only screen and (max-width: 500px) {
  .header-link {
    padding: 7px;
    font-size: 15px;
  }
}
.footer {
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    padding: 40px 0px 20px 0px;
}

.footer-element {
    margin-bottom: 20px;
    color: white;
}

.social-section {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}
    .social {
        margin: 15px;
        text-align: center;
    }
    
    .logo-name{
        font-size: 25px;
        margin-top: 40px;
    }
.home {
    text-align: center;
}

.home-background {
    text-align: center;
}
  
.home-background-image {
    min-height: 360px;
    width: 100%;
    margin-bottom: -3px;
    object-fit: cover;
    min-height: 500px;
}

.home-background-text {
    position: absolute;
    margin-top: 70px;
    width: 100%;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 36px;
    font-weight: 600;
    line-height: 52px;
}

.home-background-support-text {
    position: absolute;
    margin-top: 380px;
    width: 100%;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 18px;
    font-weight: 600;
    line-height: 30px;
}

.home-background-form {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

    .home-background-button {
        background-color: #17BC27;
        color: white;
        border-radius: 2px;
        padding: 8px;
        font-weight: bold;
        width: 150px;
        cursor: pointer;
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: 27px;
        letter-spacing: 0em;
        text-align: center;
    }
        .home-background-button:hover {
            background-color: #139b21;
        }
.sections-howitworks{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.section-title {
    font-size: 30px;
    font-weight: 600;
    line-height: 40px;
    margin: 50px 0px 40px 0px;
}
.section-howitworks-background {
    background-color: #F5F2ED;
    height: 300px;
    width: 80%;
    border-radius: 8px;
    /* box-shadow: 10px 5px 5px #d1d1d0; */
    /* box-shadow: -10px 5px 5px #d1d1d0; */
    box-shadow: 0px 5px 5px #d1d1d0;
    padding: 20px;
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1200px;
}
    .howitworks-step-title {
        font-size: 25px;
        font-weight: 600;
        margin-top: 20px;
        /* line-height: 40px; */
    }
    .howitworks-step-elements {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
        .howitworks-step-subtitle {
            font-size: 23px;
            font-weight: 500;
            line-height: 40px;
            margin-top: 40px;
            margin-bottom: 10px;
        }
        .howitworks-step-text{
            font-size: 18px;
            font-weight: 400;
            line-height: 40px;
            margin-bottom: 20px;
            /* max-width: 400px; */
        }
        .home-howitworks-image {
            height: 250px;
        }

.button-end {
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 15px 20px;
}

@media only screen and (max-width: 1300px) {
    .home-background-text {
        margin-top: 25px;
    }
    .home-background-support-text {
        margin-top: 280px;
    }
}

@media only screen and (max-width: 1100px) {
    .home-background-text {
        margin-top: 10px;
        font-size: 30px;
        line-height: 40px;
    }
    .home-background-support-text {
        margin-top: 260px;
    }
}

@media only screen and (max-width: 909px) {
    .howitworks-step-elements {
        flex-direction: column;
        align-items: center;
    }
    .section-howitworks-background {
        height: auto;
    }
    .howitworks-step-title {
        margin-bottom: 40px;
    }
    .home-howitworks-image {
        height: 200px;
    }
}

@media only screen and (max-width: 600px) {
    .home-background-text {
        font-size: 27px;
    }
}

@media only screen and (max-width: 375px) {
    .home-background-text {
        margin-top: 40px;
        font-size: 25px;
    }
    .home-background-support-text {
        margin-top: 270px;
        font-size: 16px;
    }
}

@media only screen and (max-width: 350px) {
    .home-background-text {
        margin-top: 40px;
        font-size: 23px;
    }
    .home-background-support-text {
        margin-top: 270px;
        font-size: 15px;
    }
}
.App {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
}
  
  a {
    text-decoration: none;
  }
.gameIntro {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.gameIntro-title {
    font-size: 25px;
    font-weight: bold;
    margin-top: 50px;
    margin-bottom: 40px;
}

.gameIntro-image {
    width: 100%;
}

.gameIntro-details {
    display: flex;
}
    .gameIntro-detail {
        display: flex;
        align-items: center;
        margin: 25px;
        font-size: 20px;
    }
        .gameIntro-detail-icon {
            font-size: 40px;
            margin: 6px;
        }

.gameIntro-button {
    text-decoration: none;
    padding: 15px 20px;
    background: #17BC27;
    color: white;
    border-radius: 6px;
    margin-bottom: 50px;
    font-weight: bold;
}
.timeline {
    background-color: rgb(31, 91, 221);
    color: white;
    text-align: center;
    margin-bottom: 20px;
    font-size: 13px;
    padding: 3px 0px;
    font-weight: bold;
}
.clue-background {
    display: flex;
    flex-direction: column;
    font-size: 18px;
    text-align: center;
    align-items: center;
}

.clue-background-title {
    font-size: 22px;
    margin: 20px 0px 25px 0px;
}

.clue-background-div {
    margin: 15px;
    text-align: left;
}

.question-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 18px;
    margin-top: 30px;
}

.buttons-section {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 20px;
    margin-bottom: 50px;
}

.gameNext-button {
    text-decoration: none;
    padding: 10px 20px;
    background: white;
    color: #17BC27;
    border: 1px solid #17BC27;
    border-radius: 6px;
    margin: 40px 0px 0px 0px;
    text-align: center;
    cursor: pointer;
    font-size: 18px;
}
.gameFeedback-button {
    text-decoration: none;
    background: white;
    color: #17BC27;
    margin: 40px 0px 0px 0px;
    text-align: center;
    cursor: pointer;
    font-size: 18px;
}
    .gameNext-button-below {
        text-decoration: none;
        padding: 10px 20px;
        background: white;
        color: #17BC27;
        border: 1px solid #17BC27;
        border-radius: 6px;
        margin: 20px 0px 0px 0px;
        text-align: center;
        cursor: pointer;
        font-size: 18px;
    }
    
.formInput {
    margin: 0px 5px 20px 5px;
    font-size: 18px;
}

.right {
    border: 1px solid #17BC27;
    margin: 0px 5px 20px 5px;
    font-size: 18px;
}

.wrong {
    border: 1px solid red;
    margin: 0px 5px 20px 5px;
    font-size: 18px;
}

.quizSection {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.quiz-icon {
    font-size: 35px;
}
    .quizItem {
        border: 1px solid rgb(24, 23, 23);
        border-radius: 6px;
        padding: 5px 10px;
        width: 110px;
        height: 95px;
        margin: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
        .quizItemSelected {
            border: 1px solid rgb(14, 68, 219);
            background-color: rgb(14, 68, 219);
            color: white;
            border-radius: 6px;
            padding: 5px 10px;
            width: 110px;
            height: 95px;
            margin: 5px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        .wrongItemSelected {
            border: 1px solid rgb(219, 38, 14);
            background-color: rgb(219, 38, 14);
            color: white;
            border-radius: 6px;
            padding: 5px 10px;
            width: 110px;
            height: 95px;
            margin: 5px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

/* removing the input number default increment buttons from Chrome */
input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; 
  }
/* removing the input number default increment buttons from Mozilla */
input[type=number] { 
    -moz-appearance: textfield;
    -webkit-appearance: textfield;
            appearance: textfield;
    margin: 0; 
}

