.number-input-more {
    width: 48px;
    height: 48px;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    background: #FFFFFF;
    /* box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25); */
    box-shadow: 0px 2px 4px black;
    border-radius: 0px 8px 8px 0px;
    cursor: pointer;
}
.number-input-less {
    width: 48px;
    height: 48px;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    background: #FFFFFF;
    /* box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25); */
    box-shadow: 0px 2px 4px black;
    border-radius: 8px 0px 0px 8px;
    cursor: pointer;
}

